"use client";
import { Card } from "@/components/ui/card";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { Home, Utensils, ShoppingBag, Bus } from "lucide-react";

export default function Statistics() {
  // ðŸ“Œ Weekly Graph Data
  const weeklyData = [
    { day: "Sun", thisWeek: 240000, lastWeek: 180000 },
    { day: "Mon", thisWeek: 180000, lastWeek: 220000 },
    { day: "Tue", thisWeek: 200000, lastWeek: 170000 },
    { day: "Wed", thisWeek: 220000, lastWeek: 200000 },
    { day: "Thu", thisWeek: 260000, lastWeek: 230000 },
    { day: "Fri", thisWeek: 240000, lastWeek: 190000 },
    { day: "Sat", thisWeek: 210000, lastWeek: 170000 },
  ];

  // ðŸ“Œ Dummy Expenses Data
  const expenses = [
    {
      id: 1,
      title: "Housing",
      amount: 250,
      percent: 15,
      isUp: true,
      icon: Home,
      items: [
        { name: "Rent", amount: 200, date: "Aug 20" },
        { name: "Repairs", amount: 50, date: "Aug 21" },
      ],
    },
    {
      id: 2,
      title: "Food",
      amount: 350,
      percent: 12,
      isUp: false,
      icon: Utensils,
      items: [
        { name: "Groceries", amount: 200, date: "Aug 18" },
        { name: "Dining out", amount: 150, date: "Aug 19" },
      ],
    },
    {
      id: 3,
      title: "Shopping",
      amount: 420,
      percent: 8,
      isUp: true,
      icon: ShoppingBag,
      items: [
        { name: "Clothes", amount: 300, date: "Aug 16" },
        { name: "Accessories", amount: 120, date: "Aug 17" },
      ],
    },
    {
      id: 4,
      title: "Transport",
      amount: 180,
      percent: 5,
      isUp: false,
      icon: Bus,
      items: [
        { name: "Fuel", amount: 120, date: "Aug 15" },
        { name: "Bus/Metro", amount: 60, date: "Aug 14" },
      ],
    },
  ];

  return (
    <div className="flex min-h-screen bg-gray-50">
      <main className="flex-1 p-6">
        <div className="max-w-6xl mx-auto w-full px-4 space-y-6">
          {/* ===== Chart Card ===== */}
          <Card className="p-4 rounded-lg shadow-sm bg-white w-full">
            <h2 className="text-sm font-semibold text-gray-600 mb-4">
              Weekly Comparison
            </h2>

            <ResponsiveContainer width="100%" height={300}>
              <BarChart
                data={weeklyData}
                margin={{ top: 10, right: 20, left: 20, bottom: 0 }}
              >
                <CartesianGrid strokeDasharray="3 3" vertical={false} />
                <XAxis
                  dataKey="day"
                  tick={{ fontSize: 12 }}
                  axisLine={false}
                />
                <YAxis
                  tick={{ fontSize: 12 }}
                  tickFormatter={(v) => `$${(v / 1000).toFixed(0)}k`}
                  axisLine={false}
                />
                <Tooltip
                  formatter={(value) => `$${value.toLocaleString()}`}
                  contentStyle={{ fontSize: "12px" }}
                />
                <Bar
                  dataKey="lastWeek"
                  fill="#e5e7eb"
                  radius={[6, 6, 0, 0]}
                  barSize={20}
                />
                <Bar
                  dataKey="thisWeek"
                  fill="#0d9488"
                  radius={[6, 6, 0, 0]}
                  barSize={20}
                />
              </BarChart>
            </ResponsiveContainer>
          </Card>

          {/* ===== Expenses Breakdown ===== */}
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {expenses.map((expense) => (
              <Card
                key={expense.id}
                className="p-4 rounded-xl shadow-sm bg-white flex flex-col"
              >
                {/* Header */}
                <div className="flex justify-between items-start mb-3">
                  <div>
                    <h3 className="font-semibold text-gray-700 flex items-center gap-2">
                      <expense.icon size={16} />
                      {expense.title}
                    </h3>
                    <p className="text-lg font-bold text-gray-900">
                      ${expense.amount.toFixed(2)}
                    </p>
                  </div>
                  <div className="text-right">
                    <p
                      className={`text-sm font-semibold ${
                        expense.isUp ? "text-red-500" : "text-green-500"
                      }`}
                    >
                      {expense.percent}% {expense.isUp ? "â†‘" : "â†“"}
                    </p>
                    <p className="text-xs text-gray-400">
                      Compare to last month
                    </p>
                  </div>
                </div>

                {/* Transactions list */}
                <div className="space-y-2 text-sm text-gray-600">
                  {expense.items.map((item, idx) => (
                    <div
                      key={idx}
                      className="flex justify-between border-b last:border-none pb-1"
                    >
                      <span>{item.name}</span>
                      <div className="text-right">
                        <p className="font-medium">${item.amount}</p>
                        <p className="text-xs text-gray-400">{item.date}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </Card>
            ))}
          </div>
        </div>
      </main>
    </div>
  );
}
